<template lang="pug">
    
  .messages-history
    p(v-if='!allMessages').phone__info Нет сообщений
    .all-messages(v-else ref='msggg')
      .all-messages__message(v-for='(message, i) in allMessages.list')
        p.all-messages__time(v-if='calcTime(i)').
          {{calcTime(i)}}

        .all-messages__item(
          :class="message.from == myNumber ? 'all-messages__item_my' : 'all-messages__item_sender'"
        )
          
          p {{message.text}}  

</template>

<script>

  import {mapState} from 'vuex'
  import moment from 'moment'

  export default {
    props: {
      number: [Number, String]
    },
    computed: {
      ...mapState({
        myNumber: state => state.player.phoneNumber,
        messages: state => state.messages
      }),
      allMessages() {
        return this.messages.find(({number}) => number == this.number)
      }
    },
    methods: {
      calcTime(i) {
        let msg = this.allMessages;
        let diffTime;

        diffTime = msg.list[i - 1]
          ? moment(msg.list[i - 1].time).diff(msg.list[i].time, 'hours')
          : null

        return i === 0 || diffTime <= -1
          ? moment(msg.list[i].time).calendar()
          : null
      },
      scrollBottom() {
        this.$store.dispatch('READ_MESSAGE', this.number)
        this.$nextTick(() => {
            let container = document.querySelector('.all-messages');
                container ? container.scrollTop = 9999999 : null
                
        })
      }
    },
    created() {
      this.$store.commit('REFRESH_SYSTEM_STYLE', true)
    },
    updated() {
      this.scrollBottom()
    },
    mounted() {
      this.scrollBottom()
    }
  }

</script>

<style lang="sass">

  @import '../../assets/sass/phone/apps/messages/list'

</style>