<template lang="pug">

  .messages-wrapper
    .message-header
      .message-header__wrapper
        .message-header__back(@click='goBack')
          svg.message-header__arrow
            use(xlink:href='#arrow')
          p(v-if='messagesCount > 0').message-header__count.
            {{messagesCount}}

        router-link(:to="{name: 'ContactInfo', params: {phone: number}}").message-header__title.
          {{findContact || number}}

    MessagesHistory(:number='number')

    .send-message
      input(type='text' placeholder='Сообщение' 
            v-model.trim='message' @keyup.enter='sendMessage' ref='messageInput').send-message__input
      button(@click='sendMessage').send-message__send
        img(src='@/assets/img/icons/send.svg').send-message__icon

</template>

<script>

  import {mapState, mapGetters} from 'vuex'
  import MessagesHistory from './MessagesHistory'

  export default {
    props: {
      number: [Number, String]
    },
    components: { MessagesHistory },
    data() {
      return {
        message: '',
        timeDelay: 0,
      }
    },
    computed: {
      ...mapState({
        messages:  state => state.messages,
        contacts:  state => state.calls.contacts,
        blackList: state => state.calls.blocked
      }),
      ...mapGetters({
        messagesCount: 'GET_UNREAD_MESSAGES_COUNT'
      }),
      findMessages() {
        let    msg = this.messages.find(({number}) => number == this.number)
        return msg ? msg.list : null
      },
      findContact() {
        let    contact = this.contacts.find(({number}) => number == this.number)
        return contact ? contact.name : null
      }
    },
    methods: {
      sendMessage() {
        if (!this.message)
          return this.$alert.show('Введите сообщение');

        else if (this.blackList.includes(parseInt(this.number))) 
          return this.$alert.show('Номер в черном списке')

        else if (this.timeDelay >= new Date().getTime())
          return this.$alert.show('Подождите немного...')
    
        this.$store.dispatch('SEND_MESSAGE', {msg: this.message, to: this.number});
        this.timeDelay = new Date().getTime() + 1500
        this.message = ''
        
      },
      goBack() {
        this.$router.push({name: 'Messages'})
      }
    },
    mounted() {
      this.$refs.messageInput.focus()
    }
  }

</script>
